import BigNumber from 'bignumber.js'
import erc20ABI from 'config/abi/erc20.json'
import multicall from 'utils/multicall'
import farmsConfig from 'config/constants'
import { getMasterChefAddress } from 'utils/addressHelpers'
import { FarmType } from 'config/constants/types'
import { getChefAbi } from 'hooks/useContract'
import contracts from 'config/constants/contracts'

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

export const fetchFarmUserAllowances = async (type: FarmType, account: string) => {
  const masterChefAdress = getMasterChefAddress(type)

  const calls = farmsConfig
    .filter((x) => x.type === type)
    .map((farm) => {
      const lpContractAddress = farm.isTokenOnly ? contracts[farm.tokenSymbol][CHAIN_ID] : farm.lpAddresses[CHAIN_ID]

      return { address: lpContractAddress, name: 'allowance', params: [account, masterChefAdress] }
    })

  const rawLpAllowances = await multicall(erc20ABI, calls)

  const parsedLpAllowances = rawLpAllowances.map((lpBalance) => {
    return new BigNumber(lpBalance).toJSON()
  })
  return parsedLpAllowances
}

export const fetchFarmUserTokenBalances = async (account: string) => {
  const calls = farmsConfig
    .filter((x) => x.type !== FarmType.SERVICE)
    .map((farm) => {
      const lpContractAddress = farm.isTokenOnly ? contracts[farm.tokenSymbol][CHAIN_ID] : farm.lpAddresses[CHAIN_ID]
      return {
        address: lpContractAddress,
        name: 'balanceOf',
        params: [account],
      }
    })

  const rawTokenBalances = await multicall(erc20ABI, calls)
  const parsedTokenBalances = rawTokenBalances.map((tokenBalance) => {
    return new BigNumber(tokenBalance).toJSON()
  })
  return parsedTokenBalances
}

export const fetchFarmUserStakedBalances = async (type: FarmType, account: string) => {
  const masterChefAdress = getMasterChefAddress(type)

  const calls = farmsConfig
    .filter((x) => x.type === type)
    .map((farm) => {
      return {
        address: masterChefAdress,
        name: 'userInfo',
        params: [farm.contractPid, account],
      }
    })

  const rawStakedBalances = await multicall(getChefAbi(type) as any, calls)
  const parsedStakedBalances = rawStakedBalances.map((stakedBalance) => {
    return new BigNumber(stakedBalance[0]._hex).toJSON()
  })
  return parsedStakedBalances
}

export const fetchFarmUserEarnings = async (type: FarmType, account: string) => {
  const masterChefAdress = getMasterChefAddress(type)

  const calls = farmsConfig
    .filter((x) => x.type === type)
    .map((farm) => {
      return {
        address: masterChefAdress,
        name: 'pendingReward',
        params: [farm.contractPid, account],
      }
    })

  const rawEarnings = await multicall(getChefAbi(type) as any, calls)
  const parsedEarnings = rawEarnings.map((earnings) => new BigNumber(earnings).toJSON())
  return parsedEarnings
}
