import { useEffect, useState } from 'react'
import { AbiItem } from 'web3-utils'
import { ContractOptions } from 'web3-eth-contract'
import useWeb3 from 'hooks/useWeb3'
import erc20 from 'config/abi/erc20.json'
import dollarPool from 'config/abi/dollarPool.json'
import sharePool from 'config/abi/sharePool.json'
import partnersPool from 'config/abi/partnersPool.json'
import oracle from 'config/abi/oracle.json'
import treasury from 'config/abi/treasury.json'
import boardRoom from 'config/abi/boardRoom.json'
import { FarmType, QuoteToken } from 'config/constants/types'
import { getBoardroom, getMasterChefAddress, getOracle, getTokenAddress, getTreasury } from 'utils/addressHelpers'

const dollarAbi = (dollarPool as unknown) as AbiItem
const shareAbi = (sharePool as unknown) as AbiItem
const partnersAbi = (partnersPool as unknown) as AbiItem

export const getChefAbi = (type: FarmType) => {
  if (type === FarmType.PARTNERS) return partnersAbi

  return type === FarmType.SHARE ? shareAbi : dollarAbi
}

const useContract = (abi: AbiItem, address: string, contractOptions?: ContractOptions) => {
  const web3 = useWeb3()
  const [contract, setContract] = useState(new web3.eth.Contract(abi, address, contractOptions))

  useEffect(() => {
    setContract(new web3.eth.Contract(abi, address, contractOptions))
  }, [abi, address, contractOptions, web3])

  return contract
}

export const useERC20 = (address: string) => {
  const erc20Abi = (erc20 as unknown) as AbiItem
  return useContract(erc20Abi, address)
}

export const useToken = (token: QuoteToken) => {
  return useERC20(getTokenAddress(token))
}

export const useMasterchef = (type: FarmType) => {
  return useContract(getChefAbi(type), getMasterChefAddress(type))
}

export const useBoardroom = () => {
  const boardRoomAbi = (boardRoom as unknown) as AbiItem
  return useContract(boardRoomAbi, getBoardroom())
}

export const useOracle = () => {
  const oracleAbi = (oracle as unknown) as AbiItem
  return useContract(oracleAbi, getOracle())
}

export const useTreasury = () => {
  const treasuryAbi = (treasury as unknown) as AbiItem
  return useContract(treasuryAbi, getTreasury())
}

export default useContract
