/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit'
import { FarmType } from 'config/constants/types'
import farms from 'config/constants'
import {
  fetchFarmUserEarnings,
  fetchFarmUserAllowances,
  fetchFarmUserTokenBalances,
  fetchFarmUserStakedBalances,
} from './fetchFarmUser'
import { FarmsState, Farm } from '../types'
import fetchFarms from './fetchFarms'

const initialState: FarmsState = { data: [...farms] }

export const farmsSlice = createSlice({
  name: 'Farms',
  initialState,
  reducers: {
    setFarmsPublicData: (state, action) => {
      const liveFarmsData: Farm[] = action.payload
      state.data = state.data.map((farm) => {
        const liveFarmData = liveFarmsData.find((f) => f.pid === farm.pid)
        return { ...farm, ...liveFarmData }
      })
    },
    setFarmUserData: (state, action) => {
      const { arrayOfUserDataObjects } = action.payload
      arrayOfUserDataObjects.forEach((userDataEl) => {
        const { index } = userDataEl
        state.data[index] = { ...state.data[index], userData: userDataEl }
      })
    },
  },
})

// Actions
export const { setFarmsPublicData, setFarmUserData } = farmsSlice.actions

// Thunks
export const fetchFarmsPublicDataAsync = () => async (dispatch) => {
  const f = await fetchFarms()
  dispatch(setFarmsPublicData(f))
}
export const fetchFarmUserDataAsync = (account) => async (dispatch) => {
  const userFarmAllowances = [
    ...(await fetchFarmUserAllowances(FarmType.PARTNERS, account)),
    ...(await fetchFarmUserAllowances(FarmType.DOLLAR, account)),
    ...(await fetchFarmUserAllowances(FarmType.SHARE, account)),
  ]
  const userFarmTokenBalances = await fetchFarmUserTokenBalances(account)
  const userStakedBalances = [
    ...(await fetchFarmUserStakedBalances(FarmType.PARTNERS, account)),
    ...(await fetchFarmUserStakedBalances(FarmType.DOLLAR, account)),
    ...(await fetchFarmUserStakedBalances(FarmType.SHARE, account)),
  ]
  const userFarmEarnings = [
    ...(await fetchFarmUserEarnings(FarmType.PARTNERS, account)),
    ...(await fetchFarmUserEarnings(FarmType.DOLLAR, account)),
    ...(await fetchFarmUserEarnings(FarmType.SHARE, account)),
  ]

  const arrayOfUserDataObjects = userFarmAllowances.map((farmAllowance, index) => {
    return {
      index,
      allowance: userFarmAllowances[index],
      tokenBalance: userFarmTokenBalances[index],
      stakedBalance: userStakedBalances[index],
      earnings: userFarmEarnings[index],
    }
  })

  dispatch(setFarmUserData({ arrayOfUserDataObjects }))
}

export default farmsSlice.reducer
