import BigNumber from 'bignumber.js'
import farms from 'config/constants'
import { Farm } from 'state/types'
import { QuoteToken } from 'config/constants/types'

const getFarmConfig = (pid: number) => farms.find((f) => f.pid === pid)

const calculateTotalTokenOnly = (farm: Farm, bnbPrice: BigNumber, aPrice: BigNumber): BigNumber => {
  const base = new BigNumber(farm.tokenAmount)

  if (farm.quoteTokenSymbol === QuoteToken.BNB) return base.times(bnbPrice)
  if (farm.quoteTokenSymbol === QuoteToken.A009) return base.times(aPrice)

  return base
}

export const calculateTotal = (farm: Farm, bnbPrice: BigNumber, aPrice: BigNumber): BigNumber => {
  if (farm.isTokenOnly && farm.tokenAmount) return calculateTotalTokenOnly(farm, bnbPrice, aPrice)

  const base = new BigNumber(farm.lpTotalInQuoteToken || 0)

  if (farm.quoteTokenSymbol === QuoteToken.BNB) return base.times(bnbPrice)
  if (farm.quoteTokenSymbol === QuoteToken.A009) return base.times(aPrice)

  return base
}

export default getFarmConfig
