import addresses from 'config/constants/contracts'
import { FarmType, QuoteToken } from 'config/constants/types'

const chainId = process.env.REACT_APP_CHAIN_ID

export const getTokenAddress = (token: QuoteToken) => addresses[token][chainId]

export const getMasterChefAddress = (type: FarmType) => {
  if (type === FarmType.PARTNERS) return addresses.partnerChef[chainId]
  if (type === FarmType.SHARE) return addresses.ssoChef[chainId]

  return addresses.sdoChef[chainId]
}

export const getMulticallAddress = () => addresses.mulltiCall[chainId]

export const getBoardroom = () => addresses.boardroom[chainId]

export const getTreasury = () => addresses.treasury[chainId]

export const getOracle = () => addresses.oracle[chainId]
